"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/utils/supabase/client"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { toast } from "sonner"
import { motion, AnimatePresence } from "framer-motion"
import {
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts"

type Reserva = {
  id: string
  clinic_id: string
  patient_name: string | null
  status: string | null
  starts_at: string | null
  ends_at: string | null
  price_cents: number | null
  currency: string | null
  physio_id: string | null
  created_at: string
  physiotherapist?: { id: string; name: string } | null
}

type Clinic = {
  id: string
  name: string
  location: string | null
}

export default function ClinicPanelPage() {
  const supabase = createClient()

  const [clinicId, setClinicId] = useState<string | null>(null)
  const [clinic, setClinic] = useState<Clinic | null>(null)
  const [reservas, setReservas] = useState<Reserva[]>([])
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [editing, setEditing] = useState(false)

  // üîπ Obtener cl√≠nica del usuario autenticado
  useEffect(() => {
    const fetchUserClinic = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser()
      if (!user) {
        toast.error("No se ha detectado sesi√≥n activa.")
        return
      }

      const { data: profile } = await supabase
        .from("profiles")
        .select("role, clinic_id")
        .eq("id", user.id)
        .single()

      if (!profile) return
      if (profile.role !== "clinica") return

      setClinicId(profile.clinic_id)
    }

    fetchUserClinic()
  }, [supabase])

  // üîπ Cargar cl√≠nica y reservas
  useEffect(() => {
    if (!clinicId) return

    const fetchData = async () => {
      setLoading(true)

      const { data: clinicData } = await supabase
        .from("clinics")
        .select("id, name, location")
        .eq("id", clinicId)
        .single()

      const { data: reservasData } = await supabase
        .from("reservas")
        .select("*")
        .eq("clinic_id", clinicId)

      setClinic(clinicData)
      setReservas(reservasData ?? [])
      setLoading(false)
    }

    fetchData()
  }, [clinicId, supabase])

  // üîπ Guardar cambios del formulario
  const handleSaveClinic = async () => {
    if (!clinic) return
    setSaving(true)

    const { error } = await supabase
      .from("clinics")
      .update({
        name: clinic.name,
        location: clinic.location,
      })
      .eq("id", clinic.id)

    setSaving(false)

    if (error) toast.error("Error al guardar cambios")
    else {
      toast.success("‚úÖ Cl√≠nica actualizada correctamente.")
      setEditing(false)
    }
  }

  // üîπ Datos de estad√≠sticas
  const total = reservas.length
  const accepted = reservas.filter((r) => r.status === "accepted").length
  const pending = reservas.filter((r) => r.status === "pending").length
  const cancelled = reservas.filter((r) => r.status === "cancelled").length
  const ingresos = reservas
    .filter((r) => r.status === "accepted" && r.price_cents)
    .reduce((sum, r) => sum + (r.price_cents ?? 0), 0)

  const pieData = [
    { name: "Aceptadas", value: accepted },
    { name: "Pendientes", value: pending },
    { name: "Canceladas", value: cancelled },
  ]

  const COLORS = ["#10B981", "#FACC15", "#EF4444"]

  if (loading)
    return <p className="text-center p-8 text-muted-foreground">Cargando...</p>

  // üß© Generar color din√°mico de avatar
  const colorFromName = (name: string) => {
    const hue = name
      .split("")
      .reduce((acc, char) => acc + char.charCodeAt(0), 0) % 360
    return `hsl(${hue}, 70%, 60%)`
  }

  return (
    <div className="max-w-6xl mx-auto p-8 space-y-10">
      {/* üè• Encabezado */}
      {clinic && (
        <Card className="p-6 border border-gray-300 dark:border-gray-700 shadow-sm">
          <CardHeader className="flex flex-col md:flex-row justify-between items-center gap-4">
            <div className="flex items-center gap-4">
              <motion.div
                className="flex items-center justify-center rounded-full text-white text-2xl font-bold w-14 h-14 shadow-md"
                style={{
                  background: colorFromName(clinic.name),
                }}
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ type: "spring", stiffness: 200, damping: 15 }}
              >
                {clinic.name.charAt(0).toUpperCase()}
              </motion.div>

              <div>
                <CardTitle className="text-2xl font-bold flex items-center gap-2">
                  {clinic.name}
                </CardTitle>
                <p className="text-muted-foreground text-sm">
                  üìç {clinic.location || "Ubicaci√≥n no asignada"}
                </p>
              </div>
            </div>

            <Button
              variant="outline"
              onClick={() => setEditing((prev) => !prev)}
              className="mt-4 md:mt-0"
            >
              ‚úèÔ∏è {editing ? "Cancelar" : "Editar datos"}
            </Button>
          </CardHeader>

          {/* Animaci√≥n del formulario */}
          <AnimatePresence>
            {editing && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                transition={{ duration: 0.3 }}
              >
                <CardContent className="space-y-4 mt-4">
                  <Input
                    value={clinic.name}
                    onChange={(e) =>
                      setClinic((prev) =>
                        prev ? { ...prev, name: e.target.value } : null
                      )
                    }
                    placeholder="Nombre de la cl√≠nica"
                  />
                  <Input
                    value={clinic.location ?? ""}
                    onChange={(e) =>
                      setClinic((prev) =>
                        prev ? { ...prev, location: e.target.value } : null
                      )
                    }
                    placeholder="Ubicaci√≥n"
                  />
                  <div className="flex gap-3">
                    <Button onClick={handleSaveClinic} disabled={saving}>
                      {saving ? "Guardando..." : "Guardar cambios"}
                    </Button>
                    <Button
                      variant="secondary"
                      onClick={() => setEditing(false)}
                    >
                      Cancelar
                    </Button>
                  </div>
                </CardContent>
              </motion.div>
            )}
          </AnimatePresence>
        </Card>
      )}

      {/* üìä Estad√≠sticas generales */}
      <Card className="p-6 shadow-md border border-gray-200 dark:border-gray-700">
        <CardHeader>
          <CardTitle className="text-2xl font-bold text-center">
            üìä Estad√≠sticas generales
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex flex-wrap justify-center gap-6 text-center mb-6">
            <p>‚úÖ <strong>{accepted}</strong> aceptadas</p>
            <p>‚è≥ <strong>{pending}</strong> pendientes</p>
            <p>‚ùå <strong>{cancelled}</strong> canceladas</p>
            <p>üí∂ <strong>{(ingresos / 100).toFixed(2)} ‚Ç¨</strong> ingresos</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <ResponsiveContainer width="100%" height={250}>
              <PieChart>
                <Pie
                  data={pieData}
                  dataKey="value"
                  cx="50%"
                  cy="50%"
                  outerRadius={80}
                  label
                >
                  {pieData.map((_, i) => (
                    <Cell key={i} fill={COLORS[i % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>

            <ResponsiveContainer width="100%" height={250}>
              <BarChart data={pieData}>
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Bar dataKey="value" radius={[6, 6, 0, 0]}>
                  {pieData.map((_, i) => (
                    <Cell key={i} fill={COLORS[i % COLORS.length]} />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>
        </CardContent>
      </Card>

      {/* üìÖ Reservas recientes */}
      <Card className="p-6">
        <CardHeader>
          <CardTitle className="text-2xl font-bold text-center">
            Reservas recientes
          </CardTitle>
        </CardHeader>
        <CardContent>
          {reservas.length === 0 ? (
            <p className="text-center text-muted-foreground">
              Todav√≠a no hay reservas registradas.
            </p>
          ) : (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
              {reservas.map((r) => (
                <Card key={r.id} className="border shadow-md">
                  <CardHeader>
                    <CardTitle className="text-lg font-semibold">
                      {r.patient_name ?? "Paciente"}
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-2 text-sm text-muted-foreground">
                    <p>
                      <strong>Estado:</strong> {r.status ?? "pending"}
                    </p>
                    <p>
                      <strong>Inicio:</strong>{" "}
                      {r.starts_at
                        ? new Date(r.starts_at).toLocaleString("es-ES")
                        : "‚Äî"}
                    </p>
                    <p>
                      <strong>Precio:</strong>{" "}
                      {r.price_cents
                        ? (r.price_cents / 100).toFixed(2) + " ‚Ç¨"
                        : "‚Äî"}
                    </p>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
